.video-page-container {
    padding: 20px;
    text-align: center;
  }
  
  .video-page-description {
    font-size: 18px;
    color: #333;
    max-width: 800px;
    margin: 0 auto 30px auto;
  }
  
  .video-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 20px;
    justify-content: center;
  }
  
  .video-card {
    border: 1px solid #ccc;
    padding: 15px;
    border-radius: 10px;
    background-color: #f9f9f9;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  }
  
  .video-card iframe {
    border-radius: 8px;
    max-width: 100%;
    height: 200px; 
  }
  
  .video-card h3 {
    font-size: 20px;
    margin-top: 10px;
    color: #333;
  }
  
  .video-description {
    font-size: 16px;
    color: #555;
    margin-top: 10px;
    padding: 0 15px;
  }
  
  .youtube-button {
    display: inline-block;
    margin-top: 15px;
    padding: 10px 15px;
    background-color: #ff5757;
    color: white;
    text-decoration: none;
    border-radius: 5px;
    transition: background-color 0.3s ease;
  }
  
  .youtube-button:hover {
    background-color: #ff3838;
  }
  